@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap);
html{margin:0;padding:0}body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;padding:0}h1,h2,h3,h4{margin:0;font-family:'Dancing Script', cursive}#root{display:grid;grid-template-columns:20rem auto;height:100vh}@media screen and (max-width: 1023px){#root{grid-template-columns:1fr;grid-template-rows:5rem auto}}.accessible-text{position:absolute;left:-10000000px}header{background:#33658a;padding:1rem;box-sizing:border-box;transition:all 1s;z-index:5;overflow:hidden}header #name-wrapper{display:flex;align-items:center;justify-content:flex-start;color:#fff;max-height:5rem;height:100%}header #name-wrapper h1{position:absolute;left:7rem;font-family:Lato, sans-serif}header #name-wrapper img{height:100%;max-width:100%;transition:all 0.5s}header .expand-header-button{display:none;box-sizing:border-box;z-index:10}header nav ul{list-style:none;margin:0;padding:0}header nav li{margin:2rem 0}header nav li a{color:#fff;font-size:1.5rem;font-weight:bold;text-decoration:none}@media screen and (max-width: 1023px){header nav ul{display:flex;flex-direction:column;align-items:center}header nav li{margin:1rem 0}}@media screen and (max-width: 1023px){header{height:100%}header .expand-header-button{display:inline;display:initial;height:4rem;width:4rem;position:fixed;left:unset;right:0.5rem;top:0.5rem;border:1px solid rgba(255,255,255,0.2);background:no-repeat center/60% url(/hkumar.me/static/media/menu.3b3123eb.svg) #33658a;border-radius:5px;transition:none}header #name-wrapper{justify-content:flex-start}header #name-wrapper h1{font-size:1.75rem;opacity:1;left:5.25rem;top:1.25rem;transition:all 1s}}@media screen and (max-width: 1023px){header.expanded{width:unset;height:22rem}header.expanded #name-wrapper h1{left:7.25rem;top:2.25rem}header.expanded .expand-header-button{left:unset}}#skills ul{list-style:none;padding:0;margin:0;display:grid;grid-template-columns:repeat(5, 1fr)}#skills .skill{margin:1rem;display:flex;flex-direction:column;align-items:center}#skills .skill i{font-size:4rem;transition:transform 0.5s}#skills .skill i:hover,#skills .skill i:focus{transform:scale(1.5)}@media screen and (max-width: 640px){#skills ul{grid-template-columns:repeat(3, 1fr)}#skills .skill{margin:1rem 0.5rem}}#projects ul{list-style:none;padding:0;margin:0}#projects .project{display:grid;grid-template-columns:1fr 1fr;margin:2rem 1rem 0 1rem;padding-bottom:2rem;border-bottom:1px solid #33658a}#projects .project .img-container{position:relative;background-position:top left;background-size:contain;background-repeat:no-repeat;min-height:20rem}#projects .project .content-container{padding-left:1rem}#projects .project .content-container a{color:#33658a;font-weight:bold}#projects .project .project-links{display:flex;flex-direction:row;justify-content:flex-start}#projects .project .project-links a{display:flex;align-items:center;font-size:1.25rem;margin:0 1rem 0 0;padding:0.75rem 1.25rem;background:#33658a;border-radius:20px;color:#fff;text-decoration:none;border:1px solid #33658a;box-sizing:border-box;transition:all 0.25s;box-shadow:-3px 3px 10px #000;font-weight:normal;text-align:center}#projects .project .project-links a i{margin-right:0.5rem;font-size:1.5rem}#projects .project .project-links a:hover,#projects .project .project-links a:focus{background:#fff;color:#33658a}#projects .project.reverse .img-container{grid-column-start:2;display:flex;justify-content:flex-end;background-position:top right}#projects .project.reverse .content-container{grid-column-start:1;grid-row-start:1;padding-left:0;padding-right:1rem}#projects .project:last-of-type{border-bottom:none}@media screen and (max-width: 640px){#projects .project{grid-template-columns:1fr}#projects .project .content-container{margin-top:2rem}#projects .project .img-container{background-position:top center}#projects .project.reverse .img-container{grid-column-start:1;grid-row-start:1;background-position:top center}#projects .project.reverse .content-container{grid-column-start:1;grid-row-start:2}}#experience>ul{list-style:none;margin:0;padding:0}#experience .job{margin:2rem 1rem 0 1rem}#experience .job-details{display:flex;flex-direction:column;font-size:1.125rem;line-height:1.75rem}#experience .job-header{display:flex;align-items:center;margin-bottom:0.25rem}#experience .job-header span{font-size:1.25rem;margin-left:0.5rem}#experience .job-header span:before{content:' - '}#experience .job-description{line-height:1.5rem;font-weight:bold}#experience .job-description ul{margin:1rem;padding-left:0;list-style:disc}main{height:100%;overflow:auto;padding:1rem;box-sizing:border-box;scroll-behavior:smooth}main section{margin-bottom:2rem}main section h2{color:#33658a;border-bottom:1px solid #33658a;font-size:2.5rem}main section h3{font-size:2rem;text-decoration:underline}main section p{text-align:justify;font-weight:600}#about p{margin-bottom:0.25rem}#about p:last-child{margin-top:0.25rem}#contact ul{width:100%;padding:2rem;margin:0;box-sizing:border-box;list-style:none;display:flex;flex-flow:row nowrap;justify-content:center}#contact a{padding:0.5rem 2rem;color:#000;display:flex;flex-direction:column;align-items:center;text-decoration:none}#contact a i{font-size:3rem;transition:transform 0.5s}#contact a span{color:#33658a;margin-top:0.5rem;text-decoration:underline}#contact a:hover i,#contact a:focus i{transform:scale(1.2)}

